.topBtn {
  text-align: center;
  background-color: #ED6A5A;
  color: #F5F5F5;
  width: 150px;
  height: 70px;
  font-size: 16px;
  border: none;
  border: 5px solid #0a151f;
}
.ReactWorldcup {
    margin:0;
    padding: 0;
    box-sizing:border-box;
    margin-top: 50px;
}
.RWflexBox {
    display: flex;
    flex-wrap: wrap;
    height: 70vh;
} 
.RWtitle {
    border: solid 0.2em #ed6a5a;
    color: #0a151f;
    font-size: 3rem;
    position: absolute;
    z-index: 3;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    background-color: #fff;
    padding: 0px 30px;
    text-transform: uppercase;
    padding-top: 10px;
    padding-bottom: 10px;
    margin-top: 160px;
}
.RWflex-1 {
    flex: 1;
    min-width: 500px;
    overflow: hidden;
    background-color: white;
    position: relative;
}
.RWresult-img {
    border: solid 4em white;
    margin-top: 2rem;
    width: 50%;
    position: absolute;
    left: 50%;
    bottom: 20%;
    transform: translateX(-50%);
    transition: 0.5s;
    cursor: pointer;
    overflow: hidden;
}
.RWfood-img {
    border: solid 0.5rem #0a151f;
    width: 100%;
    height: 100%;
    transition: 0.5s;
    cursor: pointer;
    opacity: 0.8;
}
.RWfood-img:hover {
    transform: scale(1.1);
    opacity: 1;
}
.RWname {
    position: absolute;
    z-index: 2;
    color: #fff;
    bottom: 10%;
    font-size: 90px;
    left: 50%;
    transform: translateX(-50%);
    background-color: #ed6a5a;
    width: 100%;
    text-align: center;
    font-weight: bold;
    font-size: 4rem;
}
.RWvs {
    position: absolute;
    z-index: 3;
    top: 58%;
    left: 50%;
    transform: translateX(-50%);
    height: 70px;
    margin-top: 0px;
}

